@import "src/common/style/sass/variables";
@import "src/common/style/sass/mixins";

.wrap {
  background-color: $footerBgColor;

  .footerContainer {
    @include maxWidth();
    min-height: 350px;

    padding-bottom: 30px;

    .footerWrap {
      @include container();
      width: 90%;

      .socialContainer {
        min-height: 150px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        @media (max-width: 800px) {
          grid-template-columns: 1fr;
        }
        justify-content: center;
        align-items: center;

        .rectangle {
          margin: 10px 10px 10px 30px;
          max-width: 306px;
          max-height: 101px;
          background-color: $footerBgColor;
        }

        .socialIconContainer {
          margin: 10px;
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: flex-end;
          flex-wrap: wrap;
          @media (max-width: 934px) {
            justify-content: center;
          }
          //align-items: flex-end;

          //margin-bottom: 50px;

          .socialWrap {
            @include flex(row, center, start, wrap);
            gap: 20px;

            .iconContainer {
              display: flex;
              justify-content: center;
              align-items: center;
              flex-wrap: wrap;
              width: 66px;
              height: 66px;
              border-radius: 50%;
              background-color: #ffffff;

              .icon {
                background-position: center;
                width: 41px;
                height: 41px;

                &:last-child {
                  margin-right: 30px;
                }
              }
            }
          }
        }
      }

      .contactsContainer {
        margin-top: 40px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        justify-content: center;
        text-align: center;
        @media (max-width: 1000px) {
          grid-template-columns: 1fr 1fr
        }
        @media (max-width: 700px) {
          grid-template-columns: 1fr
        }
        color: white;
        font-weight: 600;
        align-items: center;
        gap: 50px;

        .nameCompanyContainer {
          .name {
            font-size: 16px;
          }

        }
      }
    }
  }
}
